import HeartIcon from "vue-ionicons/dist/md-heart.vue";
export default {
  components: { HeartIcon },
  data() {
    return {
      showLoader: true,
      showErrALert: false,
      tagVariants: ["primary", "success", "warning", "info", "dark", "danger"],
      blogs: [],
    };
  },
  head: {
    title: "Blog ðŸ“š - Lydia Rodriguez",
    meta: [
      {
        hid: "description",
        name: "description",
        content:
          "Read articles written by Zumbarita Marimar, articles include topics under Zumba and many more",
      },
      {
        hid: "og:title",
        name: "og:title",
        content: "Blog ðŸ“š - Zumbaritas",
      },
      {
        property: "og:description",
        content:
          "Read articles written by Zumbarita Marimar, articles include topics under Zumba and many more.",
      },
    ],
  },
  methods: {
    // get articles hosted on dev.to
    getArticles(username, success, error) {
      let articleImage;
      fetch("https://zumbaritaslippstadt.wordpress.com/" + username)
        .then((res) => res.json())
        .then((data) => {
          data.forEach((article) => {
            if (/(hcti.io)/.test(article.image) == true) {
              // use random image from lorem picsum
              articleImage = "https://picsum.photos/600/400";
            } else {
              articleImage = article.social_image;
            }
            this.blogs.push({
              id: article.id,
              title: article.title,
              desc: article.description,
              image: articleImage,
              url: article.url,
              date: article.readable_publish_date,
              tags: article.tag_list,
              imgAlt: article.title + " - Lydia Rodriguez",
            });
          });
          success();
        })
        .catch((err) => {
          error();
          console.log("Error fetching articles " + err);
        });
    },
  },
  mounted() {
    this.getArticles(
      "lydiarodriguez",
      () => {
        this.showLoader = false;
      },
      () => {
        this.showErrALert = true;
        this.showLoader = false;
      }
    );
  },
};
